import _ from 'lodash'
import {
  LineStyle, Legend, XAixs,
  YAixs, ItemStyle, AreaStyle
} from './index'

/**
 * 
 * echarts  https://www.echartsjs.com/zh/option.html#series-line
 * @param smooth 
 * @param showSymbol 
 * @param symbol  'emptyCircle', 'circle', 'rect', 'roundRect', 'triangle', 'diamond', 'pin', 'arrow', 'none'
 * @param symbolSize 
 * @param symbolRotate 
 */
export default class LinesProprietaryConfig {
  constructor ({
    smooth = true,
    showSymbol = true,
    symbol = 'emptyCircle',
    symbolSize = 4,
    symbolRotate = 0,
    lineStyle = {},
    itemStyle = {},
    areaStyle = {},
    legend = {},
    xAxis = {},
    yAxis = {}
  }) {
    // 
    this.smooth = smooth
    this.showSymbol = showSymbol
    this.symbol = symbol
    this.symbolSize = symbolSize
    this.symbolRotate = symbolRotate
    this.lineStyle = new LineStyle(lineStyle)
    this.itemStyle = new ItemStyle(itemStyle)
    this.areaStyle = new AreaStyle(areaStyle)
    this.legend = new Legend(legend)
    this.xAxis = new XAixs(xAxis)
    this.yAxis = new YAixs(yAxis)
  }

  /**
   * 
   */
  getOption () {
    return Object.assign(_.cloneDeep(this), {
      areaStyle: this.areaStyle.getOption(),
      xAxis: this.xAxis.getOption(),
      yAxis: this.yAxis.getOption()
    })
  }
}
