<template>
  <div class="common-edge-template common-template">

    <a-collapse defaultActiveKey="1" :bordered="false">

      <!-- S  -->
      <a-collapse-panel header="" key="1">
        <a-icon slot="extra" class="icon-danger" type="delete" @click="deleteItem" />

        <EdgeTemplate :model="model" @change="change" ref="edgeTemplate">

          <template v-slot:header="{ model }">

            <div class="comment-template__item">
              <p class="comment-template__leading">:</p>
              <div class="comment-template__inner comment-template__end">
                <a-switch
                  checkedChildren=""
                  unCheckedChildren=""
                  v-model="model.display"
                  @change="displayChange" />
              </div>
            </div>
            <!-- /  -->

            <div class="comment-template__item">
              <p class="comment-template__leading">:</p>
              <div class="comment-template__inner comment-template__end">
                <a-switch
                  checkedChildren=""
                  unCheckedChildren=""
                  v-model="model.animate"
                  @change="animateChange" />
              </div>
            </div>
            <!-- /  -->

          </template>

          <template v-slot="{ model }">
            <div class="comment-template__item">
              <p class="comment-template__leading">:</p>
              <div class="comment-template__inner">
                <a-input
                  type="text"
                  v-model.trim="model.label"
                  @change="change" />
              </div>
            </div>
            <!-- /  -->

            <div class="comment-template__item">
              <p class="comment-template__leading">:</p>
              <div class="comment-template__inner">
                <ColorPicker v-model="model.labelCfg.style.fill" @change="change" />
              </div>
            </div>
            <!-- /  -->

            <div class="comment-template__item">
              <p class="comment-template__leading">:</p>
              <div class="comment-template__inner">
                <a-input
                  type="number"
                  v-model.number="model.labelCfg.style.fontSize"
                  min="0"
                  @change="change" />
              </div>
            </div>
            <!-- /  -->

            <div class="comment-template__item">
              <p class="comment-template__leading">:</p>
              <div class="comment-template__inner">
                <a-select
                  v-model="model.labelCfg.position"
                  @change="change">
                  <a-select-option value="middle"></a-select-option>
                  <a-select-option value="start"></a-select-option>
                  <a-select-option value="end"></a-select-option>
                  <a-select-option value="left"></a-select-option>
                  <a-select-option value="right"></a-select-option>
                </a-select>
              </div>
            </div>
            <!-- /  -->

          </template>

        </EdgeTemplate>

      </a-collapse-panel>
      <!-- /  -->

    </a-collapse>

  </div>
</template>

<script>
import '@/assets/less/template.less'
import _ from 'lodash'
import { mapMutations, mapState } from 'vuex'
import { ScreenMutations } from '@/store/modules/screen'
import EdgeTemplate from './edge'
import ColorPicker from '@/components/ColorPicker'
import Template from '../../template/index'

export default {
  name: 'CommonEdgeTemplate',
  components: {
    Template,
    EdgeTemplate,
    ColorPicker
  },
  computed: {
    ...mapState('screen', ['activeWidget', 'activeEdge']),
    graph () {
      return this.activeWidget.render.chart
    },
    model () {
      return _.cloneDeep(this.activeEdge.getModel())
    }
  },
  methods: {
    ...mapMutations('screen', {
      updateTopologyConfig: ScreenMutations.UPDATE_TOPOLOGY_CONFIG,
      updateEdge: ScreenMutations.ACTIVATE_EDGE
    }),
    change () {
      const { render: { chart } } = this.activeWidget
      // 
      chart.updateItem(this.model.id, this.model)
      // 
      this.updateTopologyConfig()
    },
    /**
     * 
     */
    animateChange () {
      const { render: { chart } } = this.activeWidget
      chart.setItemState(this.model.id, 'active', this.model.animate)
      this.change()
    },
    /**
     * 
     */
    displayChange () {
      this.model.display ? this.activeEdge.show() : this.activeEdge.hide()
      this.change()
    },
    /**
     * 
     * @param event
     */
    deleteItem (event) {
      const item = this.graph.findById(this.model.id)
      // 
      this.updateEdge({
        activeEdge: null
      })
      // 
      this.graph.removeItem(item)
      // 
      this.updateTopologyConfig()
      event.stopPropagation()
    }
  }
}
</script>

<style scoped>
.icon-danger {
  color: #ff4d4f;
}
</style>
