import _ from 'lodash'
import { IconProps } from './index'

export default class IconProprietaryConfig {
  constructor ({
    fontSize = 48,
    color = 'rgba(64, 169, 255, 1)',
    // props
    props = {}
  }) {
    this.fontSize = fontSize
    this.color = color
    this.props = new IconProps(props)
  }

  /**
   * 
   */
  getOption () {
    return { ..._.omit(this, ['props']) }
  }
}
