// lineDash  util 
const dashArray = [
  [0, 1],
  [0, 2],
  [1, 2],
  [0, 1, 1, 2],
  [0, 2, 1, 2],
  [1, 2, 1, 2],
  [2, 2, 1, 2],
  [3, 2, 1, 2],
  [4, 2, 1, 2]
]

const interval = 9 // lineDash 

// 
G6.registerEdge('line', {
  // setState
  setState (name, value, item) {
    const shape = item.get('keyShape')
    //  running 
    if (name === 'active') {
      // running  true 
      if (value) {
        const length = shape.getTotalLength()
        let totalArray = []
        for (var i = 0; i < length; i += interval) {
          totalArray = totalArray.concat(
            store.state.screen.activeEdge ? (store.state.screen.activeEdge.getModel().style.lineDash[0] < 4 ? [4] : store.state.screen.activeEdge.getModel().style.lineDash) : [4]
          )
        }
        let index = 0
        shape.animate({
          // 
          repeat: true,
          //  ratioNumberObject
          onFrame (ratio) {
            const cfg = {
              lineDash: dashArray[index].concat(totalArray)
            }
            index = (index + 1) % interval
            return cfg
          }
        }, 3000) //  3000
      } else {
        // 
        shape.stopAnimate()
        //  lineDash
        shape.attr('lineDash', store.state.screen.activeEdge.getModel().style.lineDash)
      }
    }
  }
}, 'line')

