const defaultLinesStaticData = {
  legend: {
    data: ['']
  },
  xAxis: {
    type: 'category',
    data: ['', '', '', '', '', '', '']
  },
  yAxis: {
    type: 'value'
  },
  series: [
    { name: '', data: [820, 932, 901, 934, 1290, 1330, 1320] }
  ]
}

export default class LinesStaticDataConfig {
  constructor ({
    staticData = defaultLinesStaticData
  }) {
    this.staticData = staticData
  }

  /**
   * 
   * @returns {string}
   */
  getCode () {
    return JSON.stringify(this.staticData, null, '\t')
  }

  /**
   * 
   * @param config
   * @param code
   */
  updateStaticData (config, code) {
    Object.assign(
      config.dataConfig.staticDataConfig,
      { staticData: JSON.parse(code) }
    )
  }
}
