import _ from 'lodash'
import G6 from '@antv/g6'
import anime from 'animejs'
import ContentMenu from '@antv/g6/build/menu'
import Chart from './index'
import store from '@/store'
import { ScreenMutations } from '@/store/modules/screen'

export default class TopologyChart extends Chart {
  constructor ({ widget }) {
    super({ widget })
    this.menuItem = null
  }

  /**
   * 
   * @param widget
   */
  init ({ config }) {
    const { commonConfig: { width, height }, proprietaryConfig } = config
    this.chart = new G6.Graph({
      container: this.container,
      width,
      height,
      renderer: 'canvas',
      plugins: [],
      modes: {
        default: [
          // 'zoom-canvas',
          // 'drag-canvas',
          // 'drag-node',
          // 'select-node',
          // {
          //   type: 'brush-select',
          //   trigger: 'ctrl',
          //   includeEdges: true
          // },
          // {
          //   type: 'tooltip', // 
          //   formatText (model) {
          //     // 
          //     return 'label: ' + model.label + '<br/>'
          //   }
          // },
          // {
          //   type: 'edge-tooltip', // 
          //   formatText (model) { // 
          //     return 'label: ' + model.label +
          //       '<br/> source: ' + model.source +
          //       '<br/> target: ' + model.target
          //   }
          // }
        ],
        edit: [
          'zoom-canvas',
          'drag-canvas',
          'drag-node',
          {
            type: 'brush-select',
            trigger: 'ctrl',
            includeEdges: true
          }
        ],
        addEdge: [
          'zoom-canvas',
          'drag-canvas',
          'drag-node',
          'add-edge',
          {
            type: 'brush-select',
            trigger: 'ctrl',
            includeEdges: true
          }
        ]
      },
      layout: {
        // type: 'dagre'
      },
      animate: true,
      // 
      nodeStateStyles: {
        //  click  true 
        active: {
          // lineWidth: 1
          // fill: '#eeeeee',
          // stroke: '#eeeeee'
        },
        inactive: {
          // fill: '#eeeeee',
          // stroke: '#eeeeee'
        }
      },
      // 
      edgeStateStyles: {
        //  click  true 
        click: {
          stroke: 'steelblue'
        },
        enter: {
          stroke: '#1890ff'
        }
      }
    })
    this.initContentMenu()
    this.read(proprietaryConfig)

    // 
    this.chart.on('wheelzoom', () => {
      console.log(this.chart.getZoom())
    })

    // 
    this.chart.on('node:contextmenu', e => {
      const menuDom = this.contentMenu.get('menu')
      anime.set(menuDom, {
        display: 'block',
        top: e.y,
        left: e.x
      })
      this.menuItem = e.item
    })

    // 
    this.chart.on('node:mouseleave', () => {
      // 
      this.hideContentMenu()
    })

    // 
    this.chart.on('edge:contextmenu', e => {
      const menuDom = this.contentMenu.get('menu')
      anime.set(menuDom, {
        display: 'block',
        top: e.y,
        left: e.x
      })
      this.menuItem = e.item
    })

    // 
    this.chart.on('edge:mouseleave', () => {
      // 
      this.hideContentMenu()
    })

    // 
    this.chart.on('node:click', ({ item }) => {
      store.commit('screen/' + ScreenMutations.ACTIVATE_NODE, {
        activeNode: item
      })
    })

    // 
    this.chart.on('node:drag', ({ item }) => {
      const activeNode = store.state.screen.activeNode
      if (activeNode) {
        store.commit('screen/' + ScreenMutations.ACTIVATE_NODE, {
          activeNode: item
        })
      }
    })

    // 
    this.chart.on('edge:mouseenter', ({ item }) => {
      this.chart.setItemState(item, 'enter', true)
    })

    // 
    this.chart.on('edge:click', ({ item }) => {
      store.commit('screen/' + ScreenMutations.ACTIVATE_EDGE, {
        activeEdge: item
      })
    })

    // 
    this.chart.on('edge:mouseout', ({ item }) => {
      this.chart.setItemState(item, 'enter', false)
    })

    // 
    this.chart.on('canvas:click', () => {
      // 
      store.commit('screen/' + ScreenMutations.ACTIVATE_NODE, {
        activeNode: null
      })
      // 
      store.commit('screen/' + ScreenMutations.ACTIVATE_EDGE, {
        activeEdge: null
      })
    })
  }

  /**
   * 
   */
  initContentMenu () {
    this.contentMenu = new ContentMenu()
    this.chart.addPlugin(this.contentMenu)
    const menuDom = this.contentMenu.get('menu')
    const p = document.createElement('p')
    p.innerText = ''
    p.onclick = () => {
      const type = this.menuItem.get('type')
      if (type === 'node') {
        // 
        if (store.state.screen.activeNode === this.menuItem) {
          // 
          store.commit('screen/' + ScreenMutations.ACTIVATE_NODE, {
            activeNode: null
          })
        }
      } else if (type === 'edge') {
        // 
        if (store.state.screen.activeEdge === this.menuItem) {
          // 
          store.commit('screen/' + ScreenMutations.ACTIVATE_EDGE, {
            activeEdge: null
          })
        }
      }
      // 
      this.hideContentMenu()
      // 
      this.chart.removeItem(this.menuItem)
      // 
      store.commit('screen/' + ScreenMutations.UPDATE_TOPOLOGY_CONFIG)
    }
    anime.set(p, {
      width: '100px',
      height: '40px',
      lineHeight: '40px',
      textAlign: 'center',
      color: '#ff4d4f',
      cursor: 'pointer',
      fontSize: '14px',
      margin: 0
    })
    menuDom.appendChild(p)
    anime.set(menuDom, {
      position: 'absolute',
      width: 100,
      height: 40,
      borderRadius: 5,
      background: 'white',
      border: '1px solid #d9d9d9',
      boxShadow: '0 2px 0 rgba(0,0,0,.015)'
    })
  }

  /**
   * 
   */
  hideContentMenu () {
    const menuDom = this.contentMenu.get('menu')
    anime.set(menuDom, {
      display: 'none'
    })
  }

  /**
   * 
   */
  read (proprietaryConfig) {
    this.chart.read(proprietaryConfig)
    const nodes = this.chart.getNodes()
    const edges = this.chart.getEdges()
    // 
    if (!_.isEmpty(nodes)) {
      nodes.forEach(node => {
        const model = node.getModel()
        this.chart.setItemState(node, model.animateType, true)
        model.display ? node.show() : node.hide()
      })
    }
    // 
    if (!_.isEmpty(edges)) {
      edges.forEach(edge => {
        const model = edge.getModel()
        this.chart.setItemState(edge, 'active', model.animate)
        model.display ? edge.show() : edge.hide()
      })
    }
  }

  /**
   * 
   * @param widget
   */
  save ({ proprietaryConfig }) {
    Object.assign(proprietaryConfig, _.cloneDeep(this.chart.save()))
  }

  /**
   * resize
   */
  resize () {
    if (this.container) {
      this.chart.changeSize(
        this.container.offsetWidth,
        this.container.offsetHeight
      )
    }
  }

  mergeOption (config) {
    this.config = config
    this.chart.zoomTo(config.proprietaryConfig.zoom)
  }

  destroy () {
    this.chart.off()
    this.chart.destroy()
  }
}
