import _ from 'lodash'

/**
 * 
 */
class Range {
  constructor (min, max) {
    this.min = min
    this.max = max
  }
}

/**
 * 
 */
class TopologyIcon {
  constructor ({
    show = false,
    position = 'inside',
    name = 'network',
    width = 64,
    height = 64,
    img = ''
  }) {
    this.show = show
    this.position = position
    this.name = name
    this.width = width
    this.height = height
    this.img = img
  }
}

/**
 * 
 */
class BorderRadius {
  constructor ({
    borderTopLeftRadius = 0,
    borderTopRightRadius = 0,
    borderBottomRightRadius = 0,
    borderBottomLeftRadius = 0
  }) {
    this.borderTopLeftRadius = borderTopLeftRadius
    this.borderTopRightRadius = borderTopRightRadius
    this.borderBottomRightRadius = borderBottomRightRadius
    this.borderBottomLeftRadius = borderBottomLeftRadius
  }
}

/**
 * 
 * @param style  solid | 'dashed' | 'dotted'
 * @param color 
 * @param width 
 * @param borderRadius 
 */
class Border {
  constructor ({
    borderStyle = 'solid',
    borderColor = '#333',
    borderWidth = 0,
    borderRadius = {}
  }) {
    this.borderStyle = borderStyle
    this.borderColor = borderColor
    this.borderWidth = borderWidth
    this.borderRadius = new BorderRadius(borderRadius)
  }

  /**
   * 
   */
  getOption () {
    return Object.assign(_.omit(_.cloneDeep(this), 'borderRadius'), {
      ...this.borderRadius
    })
  }
}

export { Range, TopologyIcon, Border }
